<template>
  <div class="personneladd">
    <v-dialog v-model="dialog" width="auto">
      <template v-slot:activator="{ on }">
        <v-icon color="red lighten-2" dark v-on="on" @click="addProcess"
          >mdi-plus-circle</v-icon
        >
      </template>

      <v-card :color="$store.state.colorSet.bgColor1">
        <v-card-title :class="titleStyle" primary-title>{{
          title
        }}</v-card-title>

        <v-card-text>
          <v-form ref="form">
            <v-row>
              <v-col cols="12" md="4">
                <v-text-field
                  label="登入授權帳號"
                  prepend-icon="mdi-codepen"
                  v-model="list.account"
                  :rules="emptyRules"
                  v-if="dialog"
                  autofocus
                ></v-text-field>
              </v-col>
              <v-col cols="12" md="4">
                <v-text-field
                  label="登入密碼"
                  prepend-icon="mdi-key-variant"
                  v-model="list.password"
                  :rules="emptyRules"
                  reauired
                ></v-text-field>
              </v-col>

              <v-col cols="12" md="4">
                <v-text-field
                  label="姓名"
                  prepend-icon="mdi-account"
                  v-model="list.name"
                  :rules="emptyRules"
                ></v-text-field>
              </v-col>
              <v-col cols="12" md="4">
                <v-text-field
                  label="手機"
                  prepend-icon="mdi-cellphone"
                  v-model="list.cellphone"
                ></v-text-field>
              </v-col>
              <v-col cols="12" md="8">
                <v-text-field
                  label="地址"
                  prepend-icon="mdi-home"
                  v-model="list.address"
                ></v-text-field>
              </v-col>
              <v-col cols="12" md="4">
                <v-text-field
                  label="緊急聯絡人1"
                  prepend-icon="mdi-account"
                  v-model="list.relationshipName1"
                ></v-text-field>
              </v-col>
              <v-col cols="12" md="4">
                <v-text-field
                  label="關係1"
                  prepend-icon="mdi-account-multiple"
                  v-model="list.relationship1"
                ></v-text-field>
              </v-col>
              <v-col cols="12" md="4">
                <v-text-field
                  label="聯絡人電話1"
                  prepend-icon="mdi-phone"
                  v-model="list.relationshipPhone1"
                ></v-text-field>
              </v-col>
              <v-col cols="12" md="4">
                <v-text-field
                  label="緊急聯絡人2"
                  prepend-icon="mdi-account"
                  v-model="list.relationshipName2"
                ></v-text-field>
              </v-col>
              <v-col cols="12" md="4">
                <v-text-field
                  label="關係2"
                  prepend-icon="mdi-account-multiple"
                  v-model="list.relationship2"
                ></v-text-field>
              </v-col>
              <v-col cols="12" md="4">
                <v-text-field
                  label="聯絡人電話2"
                  prepend-icon="mdi-phone"
                  v-model="list.relationshipPhone2"
                ></v-text-field>
              </v-col>
            </v-row>

            <v-alert outlined color="primary" text>
              <div class="d-flex">
                <header>權限設定</header>
                <v-spacer></v-spacer>
                <v-btn
                  small
                  class="mx-2"
                  color="primary"
                  @click.stop="authSelectAll"
                  >全選</v-btn
                >
                <v-btn small color="info" @click.stop="authCancelAll"
                  >全不選</v-btn
                >
              </div>
              <v-row>
                <v-col cols="12" md="6" v-for="authKey in authKeys" :key="authKey.keyName">
                  <v-alert :color="authKey.color" text v-if="authKey.keyName != 'exit'">
                    <v-row justify="space-around" class="px-3 align-center">
                      <v-checkbox :label="authKey.label" v-model="list[`${authKey.keyName}_key`]" value="true"></v-checkbox>
                      <v-row class="ml-10" justify="space-around" v-if="list[`${authKey.keyName}_key`]">
                        <v-checkbox label="新增" v-model="list[`${authKey.keyName}_add_key`]" value="true"></v-checkbox>
                        <v-checkbox label="修改" v-model="list[`${authKey.keyName}_edit_key`]" value="true"></v-checkbox>
                        <v-checkbox label="刪除" v-model="list[`${authKey.keyName}_del_key`]" value="true"></v-checkbox>
                        <v-checkbox label="查詢" v-model="list[`${authKey.keyName}_search_key`]" value="true"></v-checkbox>
                      </v-row>
                    </v-row>
                  </v-alert>
                </v-col>
                
              </v-row>
            </v-alert>
          </v-form>
        </v-card-text>

        <v-divider></v-divider>

        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn class="primary" @click="addOK" v-if="processType == 'add'"
            >確認新增</v-btn
          >
          <v-btn class="success" @click="editOK" v-if="processType == 'edit'"
            >確認修改</v-btn
          >
        </v-card-actions>
        <!-- <pre>{{ list }}</pre> -->
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import dayjs from "dayjs";

export default {
  props:['authKeys'],
  data() {
    return {
      dialog: false,
      list: {}, //表單內資料
      oldList: {}, //未修改前的表單內資料
      processType: "", //存放頁面執行是add新增或edit修改
      title: "",
      titleStyle: "",
      emptyRules: [(v) => !!v || "不可空白"],
      
    };
  },

  computed:{
    //設定權限設定的初始值,不然全選會沒功能 -> 2021/4/13 直接採用程式的方式去構築,資料來自list中的authKeys
    keyItems(){
      let keyItems = {};
      for (var authKey of this.authKeys) {
        if (authKey.keyName != "exit") {
          keyItems[`${authKey.keyName}_key`] = ""
          keyItems[`${authKey.keyName}_add_key`] = ""
          keyItems[`${authKey.keyName}_edit_key`] = ""
          keyItems[`${authKey.keyName}_del_key`] = ""
          keyItems[`${authKey.keyName}_search_key`] = ""      
        }
      }
      return keyItems;  
    }
  },

  mounted() {
    //父組件要執行本組件時的function時的設定 (showFromParent) id是暫存傳過來的值
    this.$on("fromParent", function (item) {
      this.dialog = true;
      this.editProcess(item);
    });
  },

  methods: {
    addProcess() {
      //進入畫面時,執行的是新增作業
      this.processType = "add";
      this.title = "新增人員";
      this.titleStyle = "font-weight-black error lighten-2";
      this.list = { ...this.keyItems };
      this.$nextTick(() => {
        this.$refs.form.resetValidation();
      });
    },
    editProcess(item) {
      //進入畫面時,執行的是修改作業
      this.processType = "edit";
      this.title = "修改人員資料";
      this.titleStyle = "font-weight-black success lighten-2";
      this.oldList = item; //將頁面資料與父項的資料連結
      this.list = { ...item }; //複制一份實際修改資料內容
    },
    addOK() {
      //確認新增
      if (this.$refs.form.validate()) {
        this.list.create_man =
          this.$store.state.pData.name +
          "(" +
          dayjs().format("YYYY-MM-DD HH:mm:ss") +
          ")"; //執行動作的人,時間,內容
        let postData = {
          datalist: JSON.stringify(this.list),
        };

        this.dialog = false;
        this.myAxios(
          "general/add/" + this.$store.state.databaseName + "/personnel",
          postData
        )
          .then((rs) => {
            if (rs.state == 1) {
              this.$swal({ icon: "success", text: "已新增" });
              this.$emit("getAllData");
            }
          })
          .catch((err) => {
            this.$swal({ icon: "err", text: "資料存取錯誤!!內容:" + err });
          });
      }
    },
    editOK() {
      //確認修改
      if (this.$refs.form.validate()) {
        if (this.list.edit_man == undefined) {
          this.list.edit_man = "";
        }
        this.list.edit_man =
          this.$store.state.pData.name +
          "(" +
          dayjs().format("YYYY-MM-DD HH:mm:ss") +
          ")" +
          this.list.edit_man; //執行動作的人,時間,內容
        let postData = {
          snkey: this.list.snkey,
          datalist: JSON.stringify(this.list),
        };

        this.dialog = false;
        this.myAxios(
          "general/edit/" + this.$store.state.databaseName + "/personnel",
          postData
        ).then((rs) => {
          if (rs.state == 1) {
            this.$swal({
              icon: "success",
              text: "已修改,權限相關功能將在重新登入後生效",
            });
            for (var i in this.list) {
              //將各個欄位的資料透過一對一的去比對,就可以使vue對資料會有反應
              this.oldList[i] = this.list[i];
            }
          }
        });
      }
    },
    authSelectAll() {
      for (var i in this.list) {
        if (i.includes("_key")) {
          this.list[i] = "true";
        }
      }
    },
    authCancelAll() {
      for (var i in this.list) {
        if (i.includes("_key")) {
          this.list[i] = "";
        }
      }
    },
  },
};
</script>

<style lang="scss">
