<template>
  <div class="PersonnelList">
    <v-card :color="$store.state.colorSet.bgColor2">
      <v-card-title>
        人員管理
        <v-img :src="logoImage" aspect-ratio="2.5" contain></v-img>
        <v-spacer></v-spacer>
        <v-toolbar dense>
          <v-text-field
            hide-details
            prepend-icon="mdi-magnify"
            single-line
            label="搜尋"
            v-model="searchKey"
          ></v-text-field>
          <!-- 上傳file用的input -->
          <input
            type="file"
            accept="image/*"
            ref="fileInput"
            @change="onFileSelected"
            style="display: none"
          />

          <popupadd
            ref="editProcess"
            :authKeys="authKeys"
            @getAllData="getAllData"
          ></popupadd>
        </v-toolbar>
      </v-card-title>
      <v-card-text>
        <v-data-iterator
          :items="searchfilter"
          :items-per-page.sync="itemsPerPage"
          :footer-props="{ itemsPerPageOptions }"
          locale="zh-tw"
        >
          <template v-slot:default="props">
            <v-simple-table fixed-header class="text-no-wrap"  >
              <template v-slot:default>
                <thead class="title">
                  <tr>
                    <th class="text-left"></th>
                    <!-- <th class="text-left">個人照</th> -->
                    <th class="text-left">登入帳號</th>
                    <th class="text-left">姓名</th>
                    <th class="text-left">手機</th>
                    <th class="text-left">住址</th>
                    <th class="text-left">緊急聯絡人1</th>
                    <th class="text-left">關係1</th>
                    <th class="text-left">聯絡人電話1</th>
                    <th class="text-left">緊急聯絡人2</th>
                    <th class="text-left">關係2</th>
                    <th class="text-left">聯絡人電話2</th>
                    <th class="text-left">停職</th>
                    <th class="text-left">可用權限</th>
                    <th class="text-left">創建人員</th>
                  </tr>
                </thead>
                <tbody>
                  <tr v-for="item in props.items" :key="item.snkey">
                    <td class="pa-0">
                      <v-menu transition="scale-transition" offset-y>
                        <template v-slot:activator="{ on }">
                          <v-btn icon v-on="on">
                            <img src="@/assets/gear.svg" alt="" />
                          </v-btn>
                          <!-- <v-btn color="primary" dark v-on="on"> 功能 </v-btn> -->
                        </template>
                        <v-list>
                          <LinkTimecardQrCode :list="item" />
                          <v-list-item
                            @click="edit(item)"
                            v-if="auth.personnel_edit_key"
                          >
                            <v-list-item-title>修改</v-list-item-title>
                          </v-list-item>
                          <v-list-item
                            @click="del(item)"
                            v-if="auth.personnel_del_key"
                          >
                            <v-list-item-title>刪除</v-list-item-title>
                          </v-list-item>
                        </v-list>
                      </v-menu>
                    </td>
                    <td>{{ item.account }}</td>
                    <td>{{ item.name }}</td>
                    <td>{{ item.cellphone }}</td>
                    <td>{{ item.address }}</td>
                    <td>{{ item.relationshipName1 }}</td>
                    <td>{{ item.relationship1 }}</td>
                    <td>{{ item.relationshipPhone1 }}</td>
                    <td>{{ item.relationshipName2 }}</td>
                    <td>{{ item.relationship2 }}</td>
                    <td>{{ item.relationshipPhone2 }}</td>
                    <td>
                      <v-checkbox
                        v-model="item.suspension"
                        value="true"
                        @click.stop="suspensionSet(item)"
                      ></v-checkbox>
                    </td>
                    <td>
                      <div class="d-flex">
                        <div v-for="authKey in authKeys" :key="authKey.keyName">
                          <v-chip
                            :class="authKey.class"
                            :color="authKey.color"
                            v-if="item[`${authKey.keyName}_key`]"
                            >{{ authKey.label }}</v-chip
                          >
                          <v-chip
                            :class="authKey.class"
                            :color="authKey.color"
                            v-if="item[`${authKey.keyName}_add_key`]"
                            >增</v-chip
                          >
                          <v-chip
                            :class="authKey.class"
                            :color="authKey.color"
                            v-if="item[`${authKey.keyName}_edit_key`]"
                            >修</v-chip
                          >
                          <v-chip
                            :class="authKey.class"
                            :color="authKey.color"
                            v-if="item[`${authKey.keyName}_del_key`]"
                            >刪</v-chip
                          >
                          <v-chip
                            :class="authKey.class"
                            :color="authKey.color"
                            v-if="item[`${authKey.keyName}_search_key`]"
                            >查</v-chip
                          >
                        </div>
                      </div>
                    </td>
                    <td>
                      {{ item.create_man }}
                    </td>
                  </tr>
                </tbody>
              </template>
            </v-simple-table>
          </template>
        </v-data-iterator>
      </v-card-text>
      <!-- <pre>{{items}}</pre> -->
    </v-card>
  </div>
</template>

<script>
import popupadd from "@/views/main/PersonnelAdd";
import LinkTimecardQrCode from "@/views/main/timecard/LinkTimecardQrCode";
import dayjs from "dayjs";

export default {
  components: { popupadd, LinkTimecardQrCode },
  data() {
    return {
      items: [], //目前讀取進來的資料庫
      searchKey: "",
      auth: "", //各畫面授權使用功能限定(載入放在mounted)
      itemsPerPageOptions: [10, 20, 30],
      itemsPerPage: 10,
      logoImage: require("@/assets/img/kojinjouhou_businessman.png"),
      picBaseUrl: this.$store.state.base_url + "/upload/personnel/",
      selectedFile: "",
      uploadItem: "",
    };
  },

  mounted() {
    this.auth = this.$store.state.pData;
    this.getAllData();
  },

  computed: {
    authKeys() {
      return this.$store.state.authKeys;
    },
    searchfilter() {
      var keys = this.searchKey.split(" ");
      var str = "";
      return keys.reduce(function (prev, element) {
        return prev.filter((item) => {
          str = JSON.stringify(item).toUpperCase();
          if (str.includes(element.toUpperCase())) {
            return item;
          }
        });
      }, this.items);
    },
  },

  methods: {
    getAllData() {
      //取得所有資料
      this.myAxios(
        "general/getAll/" + this.$store.state.databaseName + "/personnel"
      ).then((rs) => {
        this.items = rs.map((i) => {
          let newItem = JSON.parse(i.datalist);
          newItem.snkey = i.snkey;
          if (newItem.picname == undefined) {
            newItem.picname = "lazypic.jpg";
          }
          return newItem;
        });
      });
    },
    //修改功能
    edit(item) {
      this.$refs.editProcess.$emit("fromParent", item);
    },
    //刪除功能
    del(item) {
      //yn() 全域函數寫在main.js裡
      this.yn().then((result) => {
        /* Read more about isConfirmed, isDenied below */
        if (result.isConfirmed) {
          item.delman =
            this.$store.state.pData.name +
            "(" +
            dayjs().format("YYYY-MM-DD HH:mm:ss") +
            ")";

          var postData = {
            snkey: item.snkey,
            tablename: "personnel",
            datalist: JSON.stringify(item),
          };

          let url =
            "general/delv3/" +
            this.$store.state.databaseName +
            "/" +
            postData.tablename;

          this.myAxios(url, postData).then((rs) => {
            if (rs.state == 1) {
              this.$swal({ icon: "success", text: "已刪除!" });
              this.getAllData();
            }
          });

          //判斷是否存在圖片 -> 存在就刪掉
          if (item.picname != "lazypic.jpg") {
            console.log("delpic");
            url =
              "general/delPic/" +
              this.$store.state.databaseName +
              "/" +
              postData.tablename +
              "/" +
              item.picname;
            this.myAxios(url, postData).then((rs) => {
              console.log(rs);
            });
          }
        }
      });
    },
    //停權設定
    suspensionSet(item) {
      this.yn("確認更動停權設定!?").then((result) => {
        /* Read more about isConfirmed, isDenied below */
        if (result.isConfirmed) {
          if (item.edit_man == undefined) {
            item.edit_man = "";
          }
          item.edit_man =
            this.$store.state.pData.name +
            "(" +
            dayjs().format("YYYY-MM-DD HH:mm:ss") +
            ")" +
            item.edit_man; //執行動作的人,時間,內容

          let postData = {
            snkey: item.snkey,
            datalist: JSON.stringify(item),
          };

          this.myAxios(
            "general/edit/" + this.$store.state.databaseName + "/personnel",
            postData
          ).then((rs) => {
            // console.log(rs)
            if (rs.state == 1) {
              this.$swal({ icon: "success", text: "已修改" });
            }
          });
        } else {
          // console.log('run cancel',item.suspension)
          if (item.suspension === null) {
            item.suspension = "true";
          } else {
            item.suspension = null;
          }
        }
      });
    },
    //選取圖片
    onFileSelected(event) {
      this.selectedFile = event.target.files[0];
      var extension = this.selectedFile.name.split(".").pop().toLowerCase(); //取副檔名
      var fitType = ["gif", "png", "jpg", "jpeg"];

      var errorMsg = ""; //判斷檔案是否為圖檔及檔案大小
      if (fitType.indexOf(extension) == -1) {
        errorMsg = "檔案:" + this.selectedFile.name + "不是支持的影像檔案";
      }
      if (this.selectedFile.size > 1024 * 1024 * 5) {
        //檔案大小設定-最大5MB
        errorMsg = "檔案:" + this.selectedFile.name + "不能超過5M唷";
      }

      if (errorMsg == "") {
        this.onUpload();
      } else {
        this.$swal({ icon: "error", text: errorMsg });
        this.selectedFile = "";
      }
    },
    //執行上傳&更新原始資料
    onUpload() {
      //圖片上傳 -> 單張
      const fd = new FormData();
      fd.append("snkey", this.uploadItem.snkey); //放入上傳圖檔檔案的snkey
      fd.append("image", this.selectedFile);

      this.axios
        .post(
          "general/upload/" + this.$store.state.databaseName + "/personnel",
          fd
        )
        .then((rs) => {
          console.log(rs.data);
          this.$refs.fileInput.value = "";
          this.items.some((item) => {
            if (item.snkey == this.uploadItem.snkey) {
              item.picname = rs.data.picname;
            }
          });

          //更新原始資料
          let postData = {
            snkey: this.uploadItem.snkey,
            datalist: JSON.stringify(this.uploadItem),
          };

          this.myAxios(
            "general/edit/" + this.$store.state.databaseName + "/personnel",
            postData
          ).then((rs) => {
            console.log(rs);
          });
        });
    },
  },
};
</script>


<style lang="scss">
.PersonnelList {
  .theme--light.v-data-table
    > .v-data-table__wrapper
    > table
    > thead
    > tr
    > th {
    font-size: 1rem;
  }
}
</style>