<template>
  <div class="LinkTimecardQrCode">
    <v-dialog v-model="dialog" width="60%">
      <template v-slot:activator="{ on }">
        <v-list-item v-on="on" @click="addProcess">
          <v-list-item-title>
            綁定打卡
          </v-list-item-title>
        </v-list-item>
      </template>

      <v-card :color="$store.state.colorSet.bgColor2">
        <v-card-title :class="titleStyle" primary-title>{{
          title
        }}</v-card-title>
        <v-card-text class="mt-5 d-flex justify-center">
          <div class="mr-5">
            <qrcode-vue :value="value" :size="size" level="H" />
          </div>

          <v-radio-group v-model="type">
            <v-radio label="綁定打卡資料" value="linkQrcode"></v-radio>
            <v-radio label="取消綁定" value="unlinkQrcode"></v-radio>
            <v-radio label="打卡頁面連結" value="clockOnPage"></v-radio>
          </v-radio-group>
        </v-card-text>

      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import QrcodeVue from 'qrcode.vue'

export default {
  components: {
    QrcodeVue,
  },
  props:["list"],
  data() {
    return {
      dialog: false,
      title: "",
      titleStyle: "",
      value: "",
      size: 300,
      type:"",
      href:""
    };
  },

  watch:{
    type(){
      //建構QR CODE 內容
      let userData = {
        account:this.list.account,
        password:this.list.password,
        snkey: this.list.snkey,
        name:this.list.name,
        type:this.type,
      }
      let routeData = this.$router.resolve({path: "/timeclockon",query:{data:JSON.stringify(userData)}});
      let url = location.href;
      this.value = url.split('/#/')[0] + '/' + routeData.href;

      // window.open(routeData.href, "_blank");
    }
  },

  methods: {
    addProcess() {
      //進入畫面時,執行的是新增作業
      this.title = "綁定打卡資料 -"+ this.list.name ;
      this.titleStyle = "font-weight-black grey lighten-2";
      this.type = "linkQrcode";  //引發watch
      
    },
  },
};
</script>
